import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	data(){
		return{
			active:1,
			msg:[
				{length:3,
				videoSrc:'',
				},
				{length:0,
				videoSrc:'http://workers.lllnet.cn/video/gclc-techs/CYGR20201581243698-3.mp4?sign=22d05d9296b3ea0bbeb2a455ecc49212&t=5e846548',
				},
				{length:2,
				videoSrc:'',
				}
			],
			sh:'',
			dataList:[],
			page:1,
			pageSize:5,
			total:0,
			more:''
		}
	},
	components:{
		uniRate,
		uniLoadMore
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
	   	this.getList(this.page)
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	methods:{
		toDetail(){
			uni.navigateTo({
				url:'/pages/index/articleDetail'
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getList(p){
			var that=this
			var params={
			  page:p,
			  pagesize: this.pageSize
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='/wangtosale_list'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.allnum
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
	}
}
